import { AfterViewInit, Component, Input, OnInit, ViewChild } from '@angular/core';
import { DxDataGridComponent } from 'devextreme-angular';
import { Subscription } from 'rxjs';
import { InvoiceModel, VatService } from '@modules/vat/shared';
import { BillService } from '@modules/bill-invoice/shared/bill.service';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { ActivatedRoute, Router } from '@angular/router';
import { ProductsService } from '@modules/products/shared';
import { AuthService } from '@core/services';
import { TranslateService } from '@ngx-translate/core';
import { Helper } from '@core/helper/Helper';
import { ConfigDecimalComponent } from '@shared/modules/vat-shared/components/config-decimal/config-decimal.component';
import { VChanggeMoneyService } from '@core/library/invoice/v-changge-money.service';
import CustomStore from 'devextreme/data/custom_store';
import { BillModel } from '@modules/bill-invoice/shared/bill.model';
import { Library, sEnum, VStringService } from '@core/index';
import { pattern } from '@config/pattern';
import * as XLSX from 'xlsx';
import { element } from 'protractor';
import { InvoiceService } from '@shared/service/invoice.service';

@Component({
  selector: 'bill-product-grip',
  templateUrl: './product-grip.component.html',
  styleUrls: ['./product-grip.component.scss']
})
export class ProductGripComponent implements OnInit, AfterViewInit {
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  _translate: any;
  select: any = {};
  bsModalRef: BsModalRef;
  configs: any;
  reloadSelectProduct = true;
  extras = [];
  dataExcelFile: any = null;
  dataExcelFileInfo: any = null;
  showPopup = false
  routeSub: Subscription;
  frmData: BillModel = new BillModel(1);
  amount_after_vat: any = 0;
  ttDaXuatHoaDon = false;
  productCodePattern: any = pattern.productCodePattern;
  @Input() dataSource: any;
  @Input() formDataInInvoice: any;
  @Input() bill_products: any;
  @Input() formatter: any;
  @Input() _objTemplateSelect: any;
  @Input() isHdGTGT: any;
  rowData = 1;
  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private billService: BillService,
    private modalService: BsModalService,
    private productsService: ProductsService,
    private authService: AuthService,
    public translate: TranslateService,
    private invoiceService: InvoiceService
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());

    this.extras = [{
      id: '1',
      name: this._translate.CONTROLL.BUTTON.import_from_excel,
      items: [
        {
          id: 'LAY_MAU_BANG_KE',
          name: this._translate.CONTROLL.BUTTON.get_the_sample_file,
          icon: this._translate.ICONS.download
        },
        {
          id: 'NAP_DU_LIEU_BANG_KE',
          name: this._translate.CONTROLL.BUTTON.import_data,
          icon: this._translate.ICONS.excel
        }
      ]
    }];
  }
  lookupColumnOptions = {
    acceptCustomValue: true,
    showClearButton: true,
    onCustomItemCreating: function (args) {
      //
      //
      const newItem: any = {};
      newItem.code = VStringService._convertToViKdau(args.text);
      let oDuplicate = this.select.unit_codes.filter(p => p.code === newItem.code)[0];
      if (oDuplicate)
        newItem.code = newItem.code + Helper.addtionalUnitCode();
      newItem.name = args.text;
      this.select.unit_codes.unshift(newItem);
      setTimeout(() => {
        this.dataGrid.instance.columnOption('unit_code', 'lookup', {
          dataSource: this.select.unit_codes,
          displayExpr: 'name',
          valueExpr: 'code'
        });
      });

      return newItem;
    }.bind(this)
  };
  ngAfterViewInit() {
  }

  ngOnInit() {
    this.configs = this.authService.getConfigs();
    this.routeSub = this.route.data.subscribe((data: any) => {
      let danh_muc = this.authService.getDMLocal();
      this.select.unit_codes = danh_muc.DM_DON_VI_TINH ? danh_muc.DM_DON_VI_TINH.data : null;
      this.select.vats = danh_muc.DM_TY_LE_VAT ? danh_muc.DM_TY_LE_VAT.data : null;
      const products = data.init[0];
      let isEdit = false;
      if (data.init[2] == true)
        isEdit = true;
      this.select.products = products;
      this.select.productsView = {
        store: new CustomStore({
          key: 'code',
          loadMode: 'raw',
          load: (loadOptions) => {
            return this.select.products;
          },
          byKey: function (key) {
            const _data = data.init[0].filter(p => p.code === key.toString());
            return _data;
          }
        }),
        paginate: true,
        sort: 'name'
      };
      this.select.products.forEach(item => {
        item.mhvth = item.code + ': ' + item.name;
      });
      this.bill_products.load().then((dataSource) => {
        dataSource.forEach((item, index) => {
          if (item.product_name && item.unit_name) {
            const obj1 = this.select.unit_codes.filter(p => p.name === item.unit_name)[0];
            if (!obj1) {
              if (VStringService._getValueToString(item.unit_code) == "")
                item.unit_code = VStringService._convertToViKdau(item.unit_name);
              let oDuplicate = this.select.unit_codes.filter(p => p.code === item.unit_code)[0];
              if (oDuplicate)
                item.unit_code = item.unit_code + Helper.addtionalUnitCode();
              this.select.unit_codes.push({ code: item.unit_code, name: item.unit_name });
            }
            else {
              if (VStringService._getValueToString(item.unit_code) != "") {
                let oExistUnitCode = this.select.unit_codes.filter(p => p.code === item.unit_code)[0];
                if (!oExistUnitCode)
                  this.select.unit_codes.push({ code: item.unit_code, name: item.unit_name });
              }
            }
          }
        });
      });
      const loaiHoaDon = data.type;
      if (loaiHoaDon === sEnum.loaihoadon.GTKT01) {
        this.isHdGTGT = true;
      } else {
        this.isHdGTGT = false;
      }
    })
    this.createUpdateEvent();
  }
  _tinhGiaTri() {
    setTimeout(() => {
      VChanggeMoneyService._changeMoneyItem(this.dataSource, this.bill_products, this.formDataInInvoice, this.formatter, !this.formDataInInvoice.auto_value, this.formDataInInvoice.auto_value_price, this.select, this._objTemplateSelect, this.configs.system_parameter);
      this.amount_after_vat = this.formDataInInvoice.amount_after_vat;
      this.formDataInInvoice.amount_vat = this.formDataInInvoice.total_amount_vat;
      this.handleVat(this.formDataInInvoice);
      this.dataGrid.instance.refresh();
    }, 0);
  }
  handleVat(data) {
    let tax_information = []
    if (data.amount_vat0 === 0) {
      tax_information.push({
        "vat": "0",
        "amount": data.total_amount_product_vat0,
        "amount_vat": data.amount_vat0,
        "amount_after_vat": data.amount_after_vat0
      })
    }
    if (data.amount_vat5) {
      tax_information.push({
        "vat": "5",
        "amount": data.total_amount_product_vat5,
        "amount_vat": data.amount_vat5,
        "amount_after_vat": data.amount_after_vat5
      })
    }
    if (data.amount_vat10) {
      tax_information.push({
        "vat": "10",
        "amount": data.total_amount_product_vat10,
        "amount_vat": data.amount_vat10,
        "amount_after_vat": data.amount_after_vat10
      })
    }
    if (data.amount_vat8) {
      tax_information.push({
        "vat": "8",
        "amount": data.total_amount_product_vat8,
        "amount_vat": data.amount_vat8,
        "amount_after_vat": data.amount_after_vat8
      })
    }
    if (data.amount_vatx) {
      tax_information.push({
        "vat": "x",
        "amount": data.total_amount_product_vatx,
        "amount_vat": data.amount_vatx,
        "amount_after_vat": data.amount_after_vatx
      })
    }
    if (data.amount_vat_other) {
      tax_information.push({
        "vat": "KHAC",
        "amount": data.total_amount_product_vat_other,
        "amount_vat": data.amount_vat_other,
        "amount_after_vat": data.amount_after_vat0
      })
    }
    this.formDataInInvoice.tax_information = tax_information;
  }
  onGripKeyDown(e) {
    if (e.event.code === 'Delete') {
      e.event.srcElement.value = null;
    }
  }
  onCellPrepared(e) {
    if (e.rowType == "data") {
      if (e.column.dataField == "product_code") {
        if (VStringService._getValueToString(e.data.product_code) != "" && !this.productCodePattern.test(e.data.product_code)) {
          e.cellElement.style.background = "red url('assets/img/icons/fa-remove.png') no-repeat 2% center";
          e.cellElement.title = this._translate.VALIDATION.required_productCode;
        }
      }
    }
  }
  onRowPrepared(e) {
    if (e.rowType === 'data') {
      e.rowElement.style.whiteSpace = 'pre-wrap';
      const eles = e.rowElement.getElementsByTagName('td');
      if (!e.isEditing) {
        if (eles) {
          for (let i = 0; i < eles.length; i++) {
            if (eles[i].className.includes('dinh-khoan'))
              eles[i].innerHTML = e.data.provisions_on_accounts;
          }
          eles[2].innerHTML = e.data.product_code;
        }
      }
      if (e.data.error) {
        const ele = eles[eles.length - 1];
        ele.style.background = 'red url(\'assets/img/icons/fa-remove.png\') no-repeat 2% center';
        ele.title = e.data.error;
      }
    }
  }
  onEditorPreparing(e) {
    if (e.parentType === 'dataRow') {
      if (e.dataField === 'product_code') {
        e.editorOptions.onOpened = function (arg) {
          const popupInstance = arg.component._popup;
          popupInstance.option('width', 400);
          popupInstance.option('minWidth', 400);
        };
      }
      if (e.dataField === 'product_name') {
        e.editorName = 'dxTextArea';
      }
    }
  }
  onEditorPrepared(e) {
    if ((e.dataField === 'amount_without_discount' || e.dataField === 'discount' || e.dataField === 'amount_discount' || e.dataField === 'quantity' || e.dataField === 'price' || e.dataField === 'amount' || e.dataField === 'amount_vat' || e.dataField === 'amount_after_vat') && e.parentType === 'dataRow') {
      e.editorElement.childNodes[1].firstChild.onwheel = (evt) => {
        e.component.cancelEditData();
      };

      e.editorElement.childNodes[1].firstChild.addEventListener('select', function () {
        this.selectionStart = this.selectionEnd;
      }, false);
    }
  }
  onContextMenuPreparing(e) {

  }
  deleteRow(item) {
    setTimeout(() => {
      const check = confirm(this._translate.CONFIRM.delete);
      if (check && !this.ttDaXuatHoaDon) {
        setTimeout(() => {
          this.bill_products.remove(item.id);
          this.dataGrid.instance.refresh();
          this._tinhGiaTri();

        }, 100);
      }
    }, 200);
  }

  changeCKTM(row) {
    this._tinhGiaTri();
  }
  configDecimal(n) {
    const initialState = {
      column: n,
      formatter: this.formatter,
      showDonGiaNte: this.configs.system_parameter.view_price_nte,
    };
    this.bsModalRef = this.modalService.show(ConfigDecimalComponent, {
      class: 'modal-lg', initialState, backdrop: 'static',
      keyboard: false
    });
    const subModal = this.modalService.onHide.subscribe(() => {
      const data = this.bsModalRef.content.successSaveItem;
      if (data) {
        this.formatter[data.column] = data.character;
        this._tinhGiaTri();
      }
      subModal.unsubscribe();
    });
  }
  addRow(rowIndexFocus?) {
    if (rowIndexFocus >= 0) {
      let rowIndex = rowIndexFocus + 1;
      const data = this.frmData.add_invoice_products_data(rowIndex, rowIndex);
      if (data) {
        this.bill_products._array.splice(rowIndex, 0, data);
        let order = 0;
        this.bill_products._array.forEach(item => {
          item.order = ++order;
          item.view_order = item.order;
        });
      }
    }
    else {
      let len, order, orderLast;
      this.bill_products.load().then((dataSource) => {
        len = dataSource.length;
        orderLast = dataSource[len - 1].order;
      });

      for (let i = 0; i < this.rowData; i++) {

        if (len >= 1) {
          order = ++orderLast;
        } else {
          order = 1;
        }
        const data = this.frmData.add_invoice_products_data(order, order);
        this.bill_products.insert(data);
      }
    }
    this.dataGrid.instance.refresh();
  }
  // tnh tin 
  caculatorAmountVat(amount, vat) {
    const temp = amount / 100 * vat;
    return this.roundNumber(temp, 5);
  }
  roundNumber(number, n): number {
    let p = Math.pow(10, n);
    return Math.round(number * p) / p;
  }
  createUpdateEvent() {
    setTimeout(() => {
      this.bill_products.on('updated', (key, value) => {
        const column = Object.keys(value)[0];
        if (value.product_code !== 'add') {
          this.bill_products.byKey(key).then((row) => {
            switch (column) {
              case 'view_order':
                // this.view_orderChange();
                break;
              case 'product_code':
                this.product_codeChange(row);
                break;
              case 'unit_code':
                this.unit_codeChange(row);
                break;
              case 'country_code':
                // this.country_codeChange(row);
                break;
              case 'quantity':
              case 'price':
              case 'price_nte':
              case 'amount':
              case 'amount_vat':
              case 'amount_after_vat':
              case 'amount_discount':
              case 'discount':
              case 'amount_without_discount':
              case 'other_tax':
              case 'other_charge':
              case 'discount_on_price':
                this._tinhGiaTri();
                break;
              case 'price_after_vat':
                if ((row.vat == 5 || row.vat == 10) && row.price_after_vat)
                  row.price = (row.price_after_vat / (1 + row.vat / 100));
                else
                  row.price = row.price_after_vat;
                this._tinhGiaTri();
                break;
              case 'vat':
                if (this.configs.system_parameter.price_after_vat) {
                  if ((row.vat == 5 || row.vat == 10) && row.price_after_vat)
                    row.price = (row.price_after_vat / (1 + row.vat / 100));
                  else
                    row.price = row.price_after_vat;
                }
                this._tinhGiaTri();
                break;
              default:
                break;
              // }
            }
          }, (error) => {
            alert(error);
          });
        } else {
          this.bill_products.remove(key);
          this.addRow(null);
        }
      });
    }, 100);
  }
  product_codeChange(row) {
    this.select.products.some(item => {
      if (item.code === row.product_code) {
        this.productsService.getProduct(item.id).subscribe(rs => {
          if (rs.status == "success") {
            let product = rs.data.product;
            if (product) {
              row.product_name = product.name;
              row.unit_code = this.checkUnitCode(product.unit_code);
              row.quantity = null;
              if (this.isHdGTGT) {
                row.vat = product.vat != null ? product.vat.toString() : null;
              } else {
                row.vat = null;
              }
              // if (this.configs.system_parameter.price_after_vat) {
              //   row.price_after_vat = product.price ? parseFloat(product.price) : product.price;
              //   if ((row.vat == 5 || row.vat == 10) && row.price_after_vat)
              //     row.price = (row.price_after_vat / (1 + row.vat / 100));
              //   else
              //     row.price = row.price_after_vat;
              // }
              // else
              row.price = product.price ? parseFloat(product.price) : product.price;
              row.amount = product.price ? parseFloat(product.price) : 0;
              if (this.isHdGTGT) {
                row.amount_vat = (row.vat >= 0 || row.vat == -3) ? this.caculatorAmountVat(row.amount, row.vat) : null;
              } else {
                row.amount_vat = null;
              }
              row.amount_after_vat = row.amount + (row.amount_vat != null ? row.amount_vat : 0);
              let test = this.bill_products;
              this.bill_products.update(row.id, row);
              this._tinhGiaTri();
              return true;
            }
          }
          else {
            Library.notify(rs.message, 'error');
          }
        });
      }
    });

  }
  unit_codeChange(row) {
    const _obj = this.select.unit_codes.filter(p => p.code === row.unit_code)[0];
    if (_obj) {
      row.unit_name = _obj.name;
      this.bill_products.update(row.id, row);
    } else {
      row.unit_name = '';
      this.bill_products.update(row.id, row);
    }
  }
  checkUnitCode(unitCode) {
    const items = this.select.unit_codes;
    for (let i = 0; i < items.length; i++) {
      if (items[i].code === unitCode) {
        return unitCode;
      }
    }
    return null;
  }

  itemExtrasClick(e) {
    if (e.itemData.id === 'LAY_MAU_BANG_KE') {
      this.layMauBangKe();
    } else if (e.itemData.id === 'NAP_DU_LIEU_BANG_KE') {
      this.napDuLieuTuBangKe();
    }
  }

  // Ly mu bng k
  layMauBangKe() {
    Library.showloading();
    let fields = [];

    if (this.configs.system_parameter) {
      // din gii
      if (!this.configs.system_parameter.is_promotion) {
        fields.push('is_explain');
      }
      // Khuyn mi
      if (!this.configs.system_parameter.promotional_goods_on_the_invoice) {
        fields.push('is_promotion');
      }
      // ct khuyn mi
      if (!this.configs.system_parameter.commercial_discount) {
        fields.push('commercial_discount');
      }
    }

    if (!this.isHdGTGT) {
      fields.push('vat');
      fields.push('amount_vat');
    }

    this.billService.layFileMauMayTinhTien({
      customer_name: this.formDataInInvoice.customer_name,
      buyer_tax_code: this.formDataInInvoice.buyer_tax_code,
      buyer_address: this.formDataInInvoice.buyer_address,
      buyer_name: this.formDataInInvoice.buyer_name,
      invoice_type_name: '',
      hiddens: fields
    }).subscribe(rs => {
      if (rs.status === 'success') {
        const file = rs.data.invoice;
        Helper.dowloadFile(file.file_name, 'xlsx', file.file_content);
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => {
    }, () => {
      Library.hideloading();
    });


  }

  napDuLieuTuBangKe() {
    document.getElementById('file-bang-ke').click();
  }


  onFileBangKeChange(evt) {
    const target: DataTransfer = <DataTransfer>(evt.target);
    if (target.files.length !== 1) {
      throw new Error('Khng th chn nhiu file');
    }
    const reader: FileReader = new FileReader();
    reader.readAsBinaryString(target.files[0]);
    reader.onload = (e: any) => {
      /* read workbook */
      const bstr: string = e.target.result;
      const wb: XLSX.WorkBook = XLSX.read(bstr, { type: 'binary' });

      /* grab first sheet */
      const wsname: string = wb.SheetNames[0];
      const ws: XLSX.WorkSheet = wb.Sheets[wsname];

      /* save data */
      const data = <any>(XLSX.utils.sheet_to_json(ws, { header: 'A', range: 10 }));
      const account = <any>(XLSX.utils.sheet_to_json(ws, { header: 'A', range: 0 }));
      const fileBangKe: any = document.getElementById('file-bang-ke');


      fileBangKe.value = '';
      let _arrData: any = [];
      if (data && data[0]) {
        let columnsName = data.map(e => e.A);
        let _index = columnsName.indexOf("view_order")
        if (_index == -1) {
          Library.notify("File mu khng ng nh dng. Vui lng nhn vo Ti y  ly li file mu", "error");
          return false;
        } else {
          if (_index > 0) {
            data.splice(0, _index);
          }
          for (let i = 1; i < data.length; i++) {
            let _row: any = {}
            for (const key in data[i]) {
              if (data[i].hasOwnProperty(key)) {
                const column = data[0][key]
                const value = data[i][key];
                _row[column] = value;
              }
            }
            _arrData.push(_row);
          }
        }
      }
      this.dataExcelFile = _arrData;
      this.dataExcelFileInfo = account.slice(0, 5);
      this.showPopup = true;
    };
  }


  thayTheToanBo() {
    this.fillData(this.dataExcelFile, this.dataExcelFileInfo, 'thaythe');
    this.showPopup = false;
    this.dataExcelFile = null;
  }

  themTiep() {
    this.fillData(this.dataExcelFile, this.dataExcelFileInfo, 'themtiep');
    this.showPopup = false;
    this.dataExcelFile = null;
  }

  huy() {
    this.showPopup = false;
    this.dataExcelFile = null;
  }

  fillData(fileData, info, status) {
    if (info.length === 5) {
      const customer_name = info[0].A.split(':');
      if (customer_name.length > 1 && customer_name[1].trim() != '') {
        this.formDataInInvoice.customer_name = customer_name[1].trim();
      }
      ////
      const buyer_tax_code = info[1].A.split(':');
      if (buyer_tax_code.length > 1 && buyer_tax_code[1].trim() != '') {
        this.formDataInInvoice.buyer_tax_code = buyer_tax_code[1].trim();
      }
      ////
      const buyer_name = info[2].A.split(':');
      if (buyer_name.length > 1 && buyer_name[1].trim() != '') {
        this.formDataInInvoice.buyer_name = buyer_name[1].trim();
      }
      ////
      const buyer_address = info[3].A.split(':');
      if (buyer_address.length > 1 && buyer_address[1].trim() != '') {
        this.formDataInInvoice.buyer_address = buyer_address[1].trim();
      }
      ////
      const id_card = info[4].A.split(':');
      if (id_card.length > 1 && id_card[1].trim() != '') {
        this.formDataInInvoice.id_card = id_card[1].trim();
      }
    }

    if (status === 'thaythe') {
      this.bill_products.load().then((dataSource) => {
        dataSource.forEach((item, index) => {
          this.bill_products.remove(item.id)
        })
      })
    } else {
      this.bill_products.load().then((dataSource) => {
        dataSource.forEach((item, index) => {
          if (!item.product_code && !item.product_name && !item.quantity && !item.price && !item.amount) {
            this.bill_products.remove(item.id)
          }
        })
      })
    }

    fileData.forEach(element => {
      if (element.product_name) {
        let bill = new Object();
        bill['id'] = Helper.makeid();
        bill['order'] = element.view_order ? parseInt(element.view_order) : null;
        bill['product_code'] = element.product_code ? element.product_code : "";
        bill['product_name'] = element.product_name ? element.product_name : "";
        bill['quantity'] = element.quantity ? Helper.toNumber(element.quantity) : null;
        bill['vat'] = (element.vat && this.isHdGTGT) ? element.vat : null;
        bill["amount_vat"] = element.amount_vat ? Helper.toNumber(element.amount_vat) : null;
        bill['amount'] = element.amount ? Helper.toNumber(element.amount) : null;
        bill["unit_code"] = element.unit_name ? this.getUnitCodeByUnitName(element.unit_name) : "";
        bill["unit_name"] = element.unit_name ? element.unit_name : "";
        bill["price"] = element.price ? Helper.toNumber(element.price) : null;

        if (bill["amount_vat"] !== null && bill["amount"] !== null)
          bill["amount_after_vat"] = bill["amount_vat"] + bill["amount"];
        else
          if (bill["amount"] !== null)
            bill["amount_after_vat"] = bill["amount"];
          else
            if (bill["amount_vat"] !== null)
              bill["amount_after_vat"] = bill["amount_vat"];
            else
              bill["amount_after_vat"] = null;


        bill["is_explain"] = element.is_explain && (element.is_explain == "X" || element.is_explain == "x") ? true : false;
        bill["is_promotion"] = element.is_promotion && (element.is_promotion == "X" || element.is_promotion == "x") ? true : false;
        bill["commercial_discount"] = element.commercial_discount && (element.commercial_discount == "X" || element.commercial_discount == "x") ? true : false;


        const _obj = this.select.products.filter(p => p.code === bill["product_code"])[0];
        if (!_obj && bill["product_code"] !== '') {
          const _objPush = {
            code: bill["product_code"],
            name: bill["product_name"],
            mhvth: bill["product_code"] + ': ' + bill["product_name"],
          };
          this.select.products.push(_objPush);
        }

        this.bill_products.insert(bill);
      }
    })
    setTimeout(() => {
      this.dataGrid.instance.columnOption('unit_code', 'lookup', {
        dataSource: this.select.unit_codes,
        displayExpr: 'name',
        valueExpr: 'code'
      });
    });
    this.dataGrid.instance.refresh();
    this._tinhGiaTri();
    Library.hideloading();
  }


  getUnitCodeByUnitName(unitName) {
    if (VStringService._getValueToString(unitName) !== '') {
      if (!this.select.unit_codes || this.select.unit_codes.length == 0)
        this.select.unit_codes = []
      // if (this.select.unit_codes && Array.isArray(this.select.unit_codes)) {
      const _obj = this.select.unit_codes.filter(p => p.name === unitName)[0];
      if (_obj) {
        return _obj.code;
      } else {
        let code = VStringService._convertToViKdau(unitName);
        let oDuplicate = this.select.unit_codes.filter(p => p.code === code)[0];
        if (oDuplicate)
          code = code + Helper.addtionalUnitCode();
        this.select.unit_codes.push({ code: code, name: unitName });
        return code;
      }
      // } else {
      //   this.select.unit_codes = [];
      // }
    }
    return null;
  }
  _changeNQ43(e) {
    if (e.value) {
      if (this.formDataInInvoice.sales_percentage == null && parseInt(this.configs.system_parameter.value_rate_percent) > 0) {
        this.formDataInInvoice.sales_percentage = parseInt(this.configs.system_parameter.value_rate_percent);
      }
    }
    else {
      this.formDataInInvoice.sales_percentage = null;
    }
    this._tinhGiaTri();
  }

  onContentReady = (e) => {
    const arrFooter = [
      { name: this._translate.GRID.INVOICE_DETAILS.total_money, class: 'table-footer-1' },
      { name: this._translate.GRID.INVOICE_DETAILS.amount_vat, class: 'table-footer-2' },
      { name: this._translate.GRID.INVOICE_DETAILS.amount, class: 'table-footer-3' },
      { name: this._translate.GRID.INVOICE_DETAILS.amount_without_discount, class: 'table-footer-4' },
    ]
    this.invoiceService.formatSumColumnGrid(arrFooter);
  }
}
