import { isNull } from 'util';
import * as _ from 'lodash';
import { ViewInvoiceBillComponent } from '../view-invoice/view-invoice.component';
import { BillModel } from '@modules/bill-invoice/shared/bill.model';
import { BillService } from '@modules/bill-invoice/shared/bill.service';
import { ProductsService } from '@modules/products/shared';
import { Router } from '@angular/router';
import { ActivatedRoute } from '@angular/router';
import { MyBsModalService } from '@core/library/invoice/my-bsmodel.service';
