  fieldDisable = ["number_of_vouchers", "bill_date", "customer_code", "currency_code", "currency_rate", "buyer_tax_code", "customer_name", "buyer_name", "buyer_address", "buyer_mobile", "buyer_email", "payment_method_code", "bank_account", "id_card"];
  invoice_release_id: any;
  template_id: any;
  invoice_date = "";
  invoice_number = "";
  isAllowEditInvoiceDate = this.authService.isAllowEditInvoiceDate();
  htXuat = "XuatVaKy";
  bsModalRef: BsModalRef;
  listColumn: any = [];
  htXuatOptions = [];
  ioSignature: Subscription;
  tempKiFile = 0;
  ttDaXuatHoaDon: boolean = false; //Trng thi  xut ha n hay cha
  invoicesArr: any;
  invoice: any;
  totalInvoice = 0;
  totalErrorInvoice = 0;
  totalDuplicateInvoice = 0;
  countInvoice = 1;
  valueProgress: number = 0;
  showPopupImport = false;
  stopImport = false;
  subXuatHoaDon: any;
  subLuuChoXuat: any;
  errorImport = false;
