  checkUnitCode(unitCode) {
    let items = this.select.unit_codes;
    for (let i = 0; i < items.length; i++) {
      if (items[i].code == unitCode) {
        return unitCode;
      }
    }
    return null;
  }

  checkProvisionsOnAccounts(name) {
    let found = this.select.select_provisions_on_accounts.find(function (element) {
      return element.name === name;;
    });
    let code = "";

    if (found) {
      code = found.code;
    } else {
      Library.notify("nh khon cha c trong danh mc", "error");
    }

    return code;
  }


  caculatorAmountVat(amount, vat) {
    let temp = amount / 100 * vat;
    return this.roundNumber(temp, 5);
  }

  caculatorAmountDiscount(price, discount) {
    let temp = price * discount / 100;
    return this.roundNumber(temp, 5);
  }

  roundNumber(number, n): number {
    if (number == null) {
      return number;
    } else {
      let p = Math.pow(10, n);
      return Math.round(number * p) / p;
    }
  }

  getUnitCodeByUnitName(unitName) {
    if (VStringService._getValueToString(unitName) != "") {
      if (!this.select.unit_codes)
        this.select.unit_codes = [];
      let _obj = this.select.unit_codes.filter(p => p.name == unitName)[0];
      if (_obj) {
        return _obj.code;
      } else {
        let code = VStringService._convertToViKdau(unitName);
        let oDuplicate = this.select.unit_codes.filter(p => p.code === code)[0];
        if (oDuplicate)
          code = code + Helper.addtionalUnitCode();
        this.select.unit_codes.push({ code: code, name: unitName });
        return code;
      }
    }
    return null;
  }

  getPaymentMethodCodeByName(payment_method_name) {
    let items = this.select.payment_method_names;
    for (let i = 0; i < items.length; i++) {
      if (items[i].name == payment_method_name) {
        return items[i].code;
      }
    }
    return null;
  }
  kyThongDiep(xmlInvoice, signDate) {
    let ListSerialNumber = "DSCKS";
    let nodeSign = "//TDiep/CKSNNT";
    if (this.infoDangKy && this.infoDangKy.registration_signature) {
      let signatures = this.infoDangKy.registration_signature.filter(p => p.register_form == "THEM_MOI" || p.register_form == "GIA_HAN");
      if (signatures && signatures.length > 0)
        ListSerialNumber = signatures.map((obj: any) => obj.cert_serial).join(';');
    }
    this.socketService.kiSo(() => this.connectWebsocket(), xmlInvoice, 'thongdiep.xml', nodeSign, signDate, 'MessageData', ListSerialNumber, true);
  }

  xuLyKiFile(xmlInvoice, signDate) {
    let nodeSign = "//HDon/DSCKS/NBan";
    let ListSerialNumber = "DSCKS";
    if (this.infoDangKy && this.infoDangKy.registration_signature) {
      let signatures = this.infoDangKy.registration_signature.filter(p => p.register_form == "THEM_MOI" || p.register_form == "GIA_HAN");
      if (signatures && signatures.length > 0)
        ListSerialNumber = signatures.map((obj: any) => obj.cert_serial).join(';');
    }
    this.socketService.kiSo(() => this.connectWebsocket(), xmlInvoice, 'hoadon.xml', nodeSign, signDate, 'InvoiceData', ListSerialNumber, true);
  }

  xuLiApiKhiXuat() {
    let status;
    if (!this.invoice.id) {
      status = 'THEM_MOI';
    }
    else {
      if (!this.invoice.bill_number)
        status = 'GHI_TAM';
      else {
        if (this.invoice.status == "GIU_SO")
          status = 'GIU_SO';
        else if (this.invoice.status == "GHI_TAM")
          status = 'GHI_TAM';
      }
    }
    // if (!this.invoice.id && !this.invoice.bill_number) status = 'THEM_MOI';
    // if (this.invoice.id && !this.invoice.bill_number) status = 'GHI_TAM';
    // if (this.invoice.id && this.invoice.bill_number) status = 'GIU_SO';
    let xmlInvoice = "";
    let isNewXml = 0;
    let bill_invoice = Object.assign({}, this.invoice);
    delete bill_invoice.info_message;
    if (status == 'THEM_MOI') {
      let sub = this.billService.addNew(bill_invoice, true).subscribe(rs => {
        if (rs.status == 'success') {
          this.invoice.bill_number = rs.data.bill_number;
          this.invoice.bill_date = moment(rs.data.bill_date).format('YYYY-MM-DD HH:mm:ss');
          let bill_sign_date = moment(rs.data.sign_date).format('YYYY-MM-DD HH:mm:ss');
          this.invoice.id = rs.data.id;
          this.invoicesId.push(rs.data.id);
          xmlInvoice = VStringService._getValueToString(rs.data.xml_string);
          if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
            || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing)) {
            this.xuatTiepHoaDon('XUAT');
          } else {
            if (this.configs.system_parameter.is_seller_sign_bill)
              this.xuLyKiFile(xmlInvoice, bill_sign_date);
            else {
              this.invoice["info_message"] = rs.data.info_message;
              if (this.invoice["info_message"] && this.invoice["info_message"].message) {
                xmlInvoice = VStringService._getValueToString(this.invoice["info_message"].message.message);
                if (xmlInvoice != "") {
                  this.kyThongDiep(xmlInvoice, bill_sign_date);
                }
              }
            }
          }
        } else {
          this.showPopupImport = false;
          this.errorImport = true;
          Library.notify(rs.message, 'error');
        }
      }, err => { }, () => {
        sub.unsubscribe();
      });
    } else if (status == 'GHI_TAM') {
      let sub = this.billService.edit(bill_invoice.id, bill_invoice, true).subscribe(rs => {
        if (rs.status == 'success') {
          this.invoice.bill_number = rs.data.bill_number;
          this.frmData.bill_date = moment(rs.data.bill_date).format('YYYY-MM-DD HH:mm:ss');
          let bill_sign_date = moment(rs.data.sign_date).format('YYYY-MM-DD HH:mm:ss');
          //demo k s vo file
          //  Ly xml t serer
          isNewXml = rs.data.is_new_xml;
          if (isNewXml)
            this.nodeSign = "//HDon/DSCKS/NBan";
          else
            this.nodeSign = "//inv:transaction/inv:invoices/inv:invoice";
          xmlInvoice = VStringService._getValueToString(rs.data.xml_string);
          if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
            || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing)) {
            this.xuatTiepHoaDon('XUAT');
          }
          else {
            if (this.configs.system_parameter.is_seller_sign_bill)
              this.xuLyKiFile(xmlInvoice, bill_sign_date);
            else {
              this.invoice["info_message"] = rs.data.info_message;
              if (this.invoice["info_message"] && this.invoice["info_message"].message) {
                xmlInvoice = VStringService._getValueToString(this.invoice["info_message"].message.message);
                if (xmlInvoice != "") {
                  this.kyThongDiep(xmlInvoice, bill_sign_date);
                }
              }
            }
          }
        } else {
          this.showPopupImport = false;
          this.errorImport = true;
          Library.notify(rs.message, 'error');
        }
      }, err => { }, () => {
        sub.unsubscribe();
      });
    }
  }

