import { AfterViewInit, Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild, ViewChildren } from '@angular/core';
import { ActivatedRoute, } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { DxFormComponent } from 'devextreme-angular';
import { BsModalService } from 'ngx-bootstrap/modal';
import DataSource from 'devextreme/data/data_source';
// import { VSelectBoxService } from '@core/library/public/v-select-box.service';
import { AuthService, Helper, Library, VStringService } from '@core/index';
import { pattern } from '@config/index';
import { AddCustomerComponent } from '@shared/index';
import { TranslateService } from '@ngx-translate/core';
import { CustomersService } from '@modules/customers/shared';
import { ListsService } from '@modules/lists/shared';
import { VatService } from '@modules/vat/shared';
import { FormExtendComponent } from '@shared/components/form-extend/form-extend.component';
import * as _ from 'lodash'
import { BillService } from '@modules/bill-invoice/shared/bill.service';



@Component({
  selector: 'bill-form-invoice',
  templateUrl: './form-invoice.component.html',
  styleUrls: ['./form-invoice.component.scss']
})
export class FormInvoiceComponent implements OnInit, OnDestroy, AfterViewInit {
  _translate: any;
  routeSub: Subscription;
  select: any = [];
  emails: any = [];
  tax_codes: any = [];
  @ViewChildren(DxFormComponent) listDxForm: any;
  @ViewChild(FormExtendComponent) formExtend
  // isShow = false;
  showFormMTT = "show-form-mtt"
  customerCurent: any = {};
  readOnlyCacTruongConLai: boolean;
  pattern = pattern;
  customer_codes: any = [];
  showPopup = false;
  dataTaxCode: any;
  role_branch: boolean;
  bsModalRef: BsModalRef;
  arrEmail: any = []
  customForms = [];
  customerTempt = {};
  formItems = [];
  configs: any = {};
  listRoleBranchOrigin: Array<any>;
  @Input() frmData: any;
  @Input() ttDaXuatHoaDon: boolean = false;
  @Input() bill_products: any;
  @Input() dataSource: any;
  @Output('onFormChange') formEvent = new EventEmitter<any>();
  @Output('onTemplateTypeChange') onTemplateTypeChange = new EventEmitter<any>();
  @Input() formatter: any;
  constructor(
    private authService: AuthService,
    public translate: TranslateService,
    private route: ActivatedRoute,
    private vatService: VatService,
    private customersService: CustomersService,
    private listService: ListsService,
    private modalService: BsModalService,
    private billService: BillService
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
  }
  ngAfterViewInit() {
    let template = null;
    template = this.select.templatesTT78.find(item => item.value == this.frmData.template_id);
    const payment_method_code = this.frmData.payment_method_code;
    this.onChangeTem(template);
    if (this.frmData.id || this.billService.isCopy)
      this.frmData.payment_method_code = payment_method_code;
  }

  ngOnInit() {
    this.routeSub = this.route.data.subscribe((data: any) => {
      this.configs = this.authService.getConfigs();
      let danh_muc = this.authService.getDMLocal();
      this.select.payment_method_names = danh_muc.DM_HINH_THUC_THANH_TOAN ? danh_muc.DM_HINH_THUC_THANH_TOAN.data : null;
      const temptTT78 = data.init[1];
      if (data.init[2] == true) {
        this.select.templatesTT78 = data.init[3]
      } else {
        this.select.templatesTT78 = temptTT78;
      }
    })
    // tm kim m s thu
    this.tax_codes = new DataSource({
      load: (loadOptions) => {
        return this.vatService.getDataSourceForm(loadOptions.searchValue, 'tax_code');
      }
    });

    // // tm kim m khch hng
    this.customer_codes = new DataSource({
      load: (loadOptions) => {
        return this.vatService.getDataSourceForm(loadOptions.searchValue, 'code');
      }
    });
    if (this.frmData.buyer_email) {
      this.emails = [];
      const emailToString = this.frmData.buyer_email;
      const a11 = emailToString.replace(/;/g, ',');
      this.emails = a11.split(',');
    }
  }
  ngOnDestroy() {

  }

  onChangeTem = (e) => {
    this.select.templatesTT78.forEach(item => {
      if (e.value === item.value) {
        this.changePayment_method_code(item);
        this.formDuLieuMoRong(item);
        this.frmData.template_id = item.value;
        this.frmData.bill_series = item.bill_series;
        this.frmData.template_code = item.template_code;
        let templateSelect = {};
        for (const key in item) {
          if (item.hasOwnProperty(key)) {
            const value = item[key];
            templateSelect[key] = value;
          }
        }
        this.onTemplateTypeChange.emit(item);
      }
    });
  }

  changePayment_method_code(template) {
    if (template && template.fields) {
      const payment_method = template.fields.find(item => item.code == '3a9f145a-549a-01ea-e507-cc7b0ab28a69');
      if (payment_method && payment_method.is_show == true) {
        if (!this.frmData.payment_method_code)
          this.frmData.payment_method_code = 'TMCK'
      } else {
        this.frmData.payment_method_code = ''
      }
    }
  }

  changeDateTime() {

  }
  onAdvancedClick() {
    // this.isShow = !this.isShow;
    this.showFormMTT = this.showFormMTT ? '' : 'show-form-mtt'
  }
  customer_object_codeChange() {

  }
  customerChanged = (e) => {
    if (this.frmData.customer_id) {
      this.customersService.getCustomer(this.frmData.customer_id).subscribe(response => {
        if (response.data) {
          this.customerCurent = response.data.customer;
          if (this.customerCurent) {
            if (VStringService._getValueToString(this.customerCurent.name) !== VStringService._getValueToString(this.frmData.customer_name)
              || VStringService._getValueToString(this.customerCurent.contact_person_name) !== VStringService._getValueToString(this.frmData.buyer_name)
              || VStringService._getValueToString(this.customerCurent.contact_mobile) !== VStringService._getValueToString(this.frmData.buyer_mobile)
              || VStringService._getValueToString(this.customerCurent.business_address) !== VStringService._getValueToString(this.frmData.buyer_address)
              || VStringService._getValueToString(this.customerCurent.email) !== VStringService._getValueToString(this.frmData.buyer_email)
              || VStringService._getValueToString(this.customerCurent.bank_name) !== VStringService._getValueToString(this.frmData.bank_name)
              || VStringService._getValueToString(this.customerCurent.bank_account_number) !== VStringService._getValueToString(this.frmData.bank_account_number)) {
              this.showPopup = true;
              return;
            }
          }
        }
      });
    }
  };
  renderDataEmail($event) {
    this.arrEmail = [];
    this.emails = $event;
    if (this.emails.length > 0) {
      for (let i = 0; i < this.emails.length; i++) {
        if (typeof this.emails[i] === 'object')
          this.arrEmail.push(this.emails[i].value);
        else
          if (typeof this.emails[i] === 'string')
            this.arrEmail.push(this.emails[i]);
      }
      const emailToString = this.arrEmail.toString();
      this.frmData.buyer_email = emailToString.replace(/,/g, ';');
    } else {
      this.frmData.buyer_email = null;
    }
    this.customerChanged(null);
    this.formEvent.emit(this.frmData);
  }
  readonlyAllForm() {
    this.listDxForm._results.forEach(item => {
      item.readOnly = true;
    });
    this.readOnlyCacTruongConLai = true;
  }

  public removeReadonlyAllForm() {
    this.listDxForm._results.forEach(item => {
      item.readOnly = false;
      // item.instance.resetValues();
    });
    this.readOnlyCacTruongConLai = false;
  }
  taxCodeChoose(e) {
    const customer = e.itemData;
    if (this.frmData.customer_object_code !== 'CA_NHAN' && this.frmData.customer_object_code !== 'KHACH_LE') {
      this.frmData.customer_name = customer.name;
    }
    this.frmData.buyer_name = customer.contact_person_name;
    if (this.frmData.customer_object_code !== 'KHACH_LE') {
      this.frmData.buyer_address = customer.business_address;
    }
    this.frmData.customer_code = customer.code;
    this.frmData.buyer_mobile = customer.contact_mobile;
    if (customer.email) {
      this.frmData.buyer_email = customer.email;
      this.emails = [];
      const stringDataEmailGet = customer.email.split(';');
      for (let i = 0; i < stringDataEmailGet.length; i++) {
        this.emails.push({ display: stringDataEmailGet[i], value: stringDataEmailGet[i] });
      }
    } else {
      this.frmData.buyer_email = customer.email;
      this.emails = [];
    }
    this.frmData.customer_id = customer.id;
    this.frmData.bank_account_number = customer.bank_account_number ? customer.bank_account_number : '';
    this.frmData.bank_name = customer.bank_name ? customer.bank_name : '';
    ////
    this.onFieldDataChanged();
  }
  changeTaxCode(e) {
    this.frmData.buyer_tax_code = e.value
  }
  updateTaxcodeInfo = (e) => {
    if (this.frmData.customer_id) {
      this.customersService.getCustomer(this.frmData.customer_id).subscribe(response => {
        if (response.data) {
          this.customerCurent = response.data.customer;
          if (this.customerCurent) {
            if (VStringService._getValueToString(this.customerCurent.tax_code) !== VStringService._getValueToString(this.frmData.buyer_tax_code)) {
              this.showPopup = true;
              return;
            }
          }
        }
      });
    }
  };
  searchTaxCode() {
    Library.showloading();
    if (!this.frmData.buyer_tax_code) {
      Library.notify("Vui lng nhp m s thu!", "error")
    } else {
      this.vatService.getInfoCompany(this.frmData.buyer_tax_code).subscribe(res => {
        if (res.status == "success") {
          this.dataTaxCode = res.data.customer;
          this.frmData.customer_name = this.dataTaxCode.name;
          this.frmData.buyer_address = this.dataTaxCode.business_address;
        }
      })
    }
    setTimeout(() => {
      Library.hideloading();
    }, 1000);
  }
  customerCodeChoose(e) {
    const customer = e.itemData;
    if (this.frmData.customer_object_code !== 'CA_NHAN' && this.frmData.customer_object_code !== 'KHACH_LE') {
      this.frmData.customer_name = customer.name;
    }
    this.frmData.buyer_name = customer.contact_person_name;
    if (this.frmData.customer_object_code !== 'KHACH_LE') {
      this.frmData.buyer_address = customer.business_address;
    }
    this.frmData.buyer_tax_code = customer.tax_code;
    this.frmData.buyer_mobile = customer.contact_mobile;
    if (customer.email) {
      this.frmData.buyer_email = customer.email;
      this.emails = [];
      const stringDataEmailGet = customer.email.split(';');
      for (let i = 0; i < stringDataEmailGet.length; i++) {
        this.emails.push({ display: stringDataEmailGet[i], value: stringDataEmailGet[i] });
      }
    } else {
      this.frmData.buyer_email = customer.email;
      this.emails = [];
    }
    this.frmData.customer_id = customer.id;
    this.frmData.bank_account_number = customer.bank_account_number ? customer.bank_account_number : '';
    this.frmData.bank_name = customer.bank_name ? customer.bank_name : '';
    this.onFieldDataChanged();
  }
  updateCustomerInfo = (e) => {
    if (this.frmData.customer_id) {
      this.customersService.getCustomer(this.frmData.customer_id).subscribe(response => {
        if (response.data) {
          this.customerCurent = response.data.customer;
          if (this.customerCurent) {
            if (VStringService._getValueToString(this.customerCurent.code) !== VStringService._getValueToString(this.frmData.customer_code)) {
              this.showPopup = true;
              return;
            }
          }
        }
      });
    }
  };
  async editTaxCode() {
    this.listRoleBranchOrigin = [];
    if (this.role_branch) {
      const roleUser = await this.listService.getDataUserLocalStorege();
      if (roleUser) {
        this.listRoleBranchOrigin = await this.handleListRoleBranch(roleUser);
      } else {
        this.listRoleBranchOrigin = await this.listService.handleListRoleBranchUser();
      }
      if (Array.isArray(this.listRoleBranchOrigin) && this.listRoleBranchOrigin.length) {
        if (this.listRoleBranchOrigin.length === 2) {
          this.listRoleBranchOrigin = this.listRoleBranchOrigin.filter(value => {
            if (value.code !== 'TAT_CA') {
              value.checked = true;
              return value;
            }
          });
        }
      }
    }
    const initialState = {
      isVisibleGhiVaThemMoi: false,
      role_branch: this.role_branch,
      roleBranchStatus: this.listRoleBranchOrigin
    };
    this.bsModalRef = this.modalService.show(AddCustomerComponent, {
      class: 'modal-lg full-modal', initialState, backdrop: 'static',
      keyboard: false
    });
    const submodal = this.modalService.onHide.subscribe(() => {
      if (this.bsModalRef.content.successSaveData) {
        const customer = this.bsModalRef.content.successSaveData;
        this.frmData.customer_name = customer.name;
        this.frmData.buyer_tax_code = customer.tax_code;
        this.frmData.buyer_name = customer.contact_person_name;
        this.frmData.buyer_address = customer.business_address;
        this.frmData.customer_code = customer.code;
        this.frmData.buyer_mobile = customer.contact_mobile;

        if (customer.email) {
          this.frmData.buyer_email = customer.email;
          this.emails = [];
          const stringDataEmailGet = customer.email.split(';');
          for (let i = 0; i < stringDataEmailGet.length; i++) {
            this.emails.push({ display: stringDataEmailGet[i], value: stringDataEmailGet[i] });
          }
        } else {
          this.frmData.buyer_email = customer.email;
          this.emails = [];
        }
        if (customer.bank_account_number) {
          this.frmData.bank_account_number = customer.bank_account_number;
        }
        if (customer.bank_name) {
          this.frmData.bank_name = customer.bank_name;
        }
      }
      submodal.unsubscribe();
    });
  }
  onFieldDataChanged() {
    this.formEvent.emit(this.frmData);
  }
  async handleListRoleBranch(roleUser) {
    let arrList: any;
    arrList = await this.listService.handleGetDMtoCustomer(roleUser);
    if (Array.isArray(arrList) && arrList.length) {
      return this.listService.handleCheckAll(arrList);
    }
    return null;
  }
  capNhatDmKhachHang() {
    this.customerCurent.tax_code = VStringService._getValueToString(this.frmData.buyer_tax_code);
    this.customerCurent.name = VStringService._getValueToString(this.frmData.customer_name);
    this.customerCurent.code = VStringService._getValueToString(this.frmData.customer_code);
    this.customerCurent.contact_person_name = VStringService._getValueToString(this.frmData.buyer_name);
    this.customerCurent.contact_mobile = VStringService._getValueToString(this.frmData.buyer_mobile);
    this.customerCurent.business_address = VStringService._getValueToString(this.frmData.buyer_address);
    this.customerCurent.email = VStringService._getValueToString(this.frmData.buyer_email);
    this.customerCurent.bank_name = VStringService._getValueToString(this.frmData.bank_name);
    this.customerCurent.bank_account_number = VStringService._getValueToString(this.frmData.bank_account_number);
    const data = { customer: this.customerCurent };
    this.customersService.edit(data, this.customerCurent.id).subscribe((response: any) => {
      if (response.status === 'success') {
        this.showPopup = false;
      } else {
        Library.notify(response.message, 'error');
      }
    }, err => {
    }, () => {
    });
  }
  huy() {
    this.showPopup = false;
  }
  paymentMethodChange = (e) => {
    const _obj = this.select.payment_method_names.filter(p => p.code === e.value)[0];
    if (_obj) {
      this.frmData.payment_method_name = _obj.name;
    }
  };

  // reset trng m rng
  public resetTMR() {
    this.formExtend.resetForm();
    this.formExtend.resetOptions();
  }


  formDuLieuMoRong(item) {
    let _dulieumorong = _.cloneDeep(item.fields).filter(x => x.type === '4');
    Helper.resetObject(this.formItems)
    this.customForms = [];
    this.customerTempt = {};
    const bill_options = this.frmData.bill_options;

    if (bill_options.length > 0) {
      _dulieumorong = _dulieumorong.map((dl) => {
        let option = bill_options.find(x => x.code == dl.code)
        if (option) {
          dl.value = option.value;
        }
        return dl;
      });
    }

    this.frmData.bill_options = _dulieumorong;
    if (_dulieumorong.length > 0) {
      this.formExtend.createForm(this.customForms, _dulieumorong)
    }
    _dulieumorong.forEach(value => {
      this.customerTempt[value.code] = value.value;
    });
  }
  disableTemplateInput() {
    if (this.frmData.bill_number) {
      return true;
    }
    return false;
  }
}
