import { Component, OnInit } from '@angular/core';

import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import * as moment from 'moment';

import { Helper, Library, SocketService, AuthService, VStringService, VPreviewInvService, sEnum } from '@core/index';
import { VatService } from '@modules/vat/shared';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { BillService } from '@modules/bill-invoice/shared/bill.service';
import { BsModalService } from 'ngx-bootstrap';
import { ViewInvoiceMultipageComponent } from '@shared/components';
import { SignatureOffComponent } from '@shared/index';
import { MyBsModalService } from '@core/library/invoice/my-bsmodel.service';
import { ViewInvoiceService } from '@shared/service/view-invoice.service';
@Component({
  selector: 'app-send-invoice-to-tax',
  templateUrl: './send-invoice-to-tax.component.html',
  styleUrls: ['./send-invoice-to-tax.component.scss']
})
export class SendInvoiceToTaxComponent implements OnInit {

  invoices: any;
  configs: any;
  invoiceList: any;
  ioSignature: any;
  invoiceIndex = 0;
  tempKiFile = 0;
  tempTemplate: any;
  daXong: boolean = false;
  ganXong: boolean = false;

  valueProgress: number = 0;
  errorImport: boolean = false;
  subXuatHoaDon: any;
  showPopup = false;
  totalInvoice: number;
  _translate: any;
  nodeSign: any;
  isExportFailed: boolean = false;
  reportCountERR: any = "";
  isTT78: boolean = false;
  infoDangKy: any;
  isHdGTGT: any;
  search: any;
  dulieu: any;
  format = (value) => {
    let percent = Math.round(value * 100);
    return this._translate.NOTIFY.finish + ": " + percent + '%';
  }

  constructor(
    public bsModalRef: BsModalRef,
    private vatService: VatService,
    private socketService: SocketService,
    private authService: AuthService,
    private router: Router,
    private spinner: NgxSpinnerService,
    private billService: BillService,
    private modalImportService: MyBsModalService,
    private modalService: BsModalService,
    private vPreviewInvService: ViewInvoiceService,
  ) {
    this._translate = this.authService.getTranslate();
  }

  ngOnInit() {
    this.configs = this.authService.getConfigs();
    this.infoDangKy = this.authService.getInfoRegister();
    if (this.infoDangKy) {
      if (this.infoDangKy.status == "CHAP_NHAN")
        this.isTT78 = true;
    }
    this.connectWebsocket();
  }

  onImportShowing(e) {
    this.valueProgress = 0;
  }

  onImportHiding(e) {
    let r = true;
    if (this.valueProgress < 100 && this.errorImport == false) {
      r = confirm(this._translate.CONFIRM.sign_stop);
    }
    if (r) {
      if (this.subXuatHoaDon) {
        this.subXuatHoaDon.unsubscribe();
      }
      this.valueProgress = 0;
    } else {
      e.cancel = true;
    }
  }

  connectWebsocket() {
    this.ioSignature = this.socketService.lapHoaDonImport().subscribe(message => {
      let data: any = message;
      if (data.Status == 'success') {
        if (data.SvcName == "efy_Sign") {
          let i = this.invoiceIndex;
          if (data.Data.Name == 'thongdiep.xml') {
            this.guiThongDiepDaKy(data.Data.Content);
          } else if (data.Data.Name == 'ten_filek_the_trung-dc-nhi.xml') {
            this.taoThongDiep();
          }
        }
      } else {
        const popUp = window.open('about:blank', 'name', 'width=200, height=100');
        if (popUp == null || typeof (popUp) == 'undefined') {
        }
        else
          popUp.close();
        this.spinner.hide();
        if (data.Description == "") {
          this.showPopup = false;
          this.errorImport = true;
        }
      }
    });
  }

  guiThongDiepDaKy(xml_string) {
    if (this.dulieu != null) {
      this.dulieu["message"]["message"] = xml_string;
      Library.showloading();
      this.subXuatHoaDon = this.billService.guiThongDiep(this.dulieu).subscribe(rs => {
        if (rs.status == 'success') {
          this.invoiceList.forEach(item => {
            item.status = rs.data.status;
          });
          Library.notify(this._translate.THONG_TIN.gui_hoa_don_cqt_thanh_cong, 'success');
          this.daXong = true;
        } else {
          Library.notify(rs.message, 'error');
        }
      }, err => { }, () => {
        Library.hideloading();
        this.subXuatHoaDon.unsubscribe();
      });
    }
    else {
      Library.notify(this._translate.THONG_TIN.khong_co_du_lieu_gui_cqt, "error");
    }
  }

  guiCQT() {
    Library.confirmVA(this._translate.THONG_TIN.cau_hoi_xac_nhan_gui_cqt, this._translate.NOTIFY.notify, () => {
      this.dulieu = null;
      if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
        || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing)) {
        this.taoThongDiep();
      } else {
        let ListSerialNumber = "DSCKS";
        if (this.infoDangKy && this.infoDangKy.registration_signature) {
          let signatures = this.infoDangKy.registration_signature.filter(p => p.register_form == "THEM_MOI" || p.register_form == "GIA_HAN");
          if (signatures && signatures.length > 0)
            ListSerialNumber = signatures.map((obj: any) => obj.cert_serial).join(';');
        }
        let invoiceDate = moment(new Date()).format('YYYY-MM-DD HH:mm:ss');
        this.socketService.kiSo(() => this.connectWebsocket(), 'PHRlbXBJbnZvaWNlPjwvdGVtcEludm9pY2U+', 'ten_filek_the_trung-dc-nhi.xml', '//tempInvoice', invoiceDate, "", ListSerialNumber, true);
      }
    });
  }
  taoThongDiep() {
    let ids = this.invoiceList.map(item => item.id);
    Library.showloading();
    let sub = this.billService.layThongDiep(ids).subscribe(rs => {
      if (rs.status == 'success') {
        if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
          || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing)) {
          this.invoiceList.forEach(item => {
            item.status = rs.data.status;
          });
          Library.notify(this._translate.THONG_TIN.gui_hoa_don_cqt_thanh_cong, 'success');
          this.daXong = true;
        }
        else {
          this.dulieu = rs.data;
          let bill_sign_date = moment(rs.data.sign_date).format('YYYY-MM-DD HH:mm:ss');
          this.xuLyKiFile(rs.data.message.message, bill_sign_date);
        }
      }
      else {
        Library.notify(rs.message, 'error');
      }
    },
      err => { },
      () => {
        sub.unsubscribe();
        Library.hideloading();
      });
  }

  xuLyKiFile(xmlInvoice, signDate) {
    let ListSerialNumber = "DSCKS";
    let nodeSign = "//TDiep/CKSNNT";
    if (this.infoDangKy && this.infoDangKy.registration_signature) {
      let signatures = this.infoDangKy.registration_signature.filter(p => p.register_form == "THEM_MOI" || p.register_form == "GIA_HAN");
      if (signatures && signatures.length > 0)
        ListSerialNumber = signatures.map((obj: any) => obj.cert_serial).join(';');
    }
    this.socketService.kiSo(() => this.connectWebsocket(), xmlInvoice, 'thongdiep.xml', nodeSign, signDate, 'MessageData', ListSerialNumber, true);
  }

  dong() {
    if (this.daXong) {
      this.bsModalRef.content.successSaveItem = "success";
    }
    this.bsModalRef.hide();
    if (this.isHdGTGT) {
      if (this.search)
        this.router.navigate([`/system/bills-invoice-vat`], { queryParams: this.search });
      else
        this.router.navigate([`/system/bills-invoice-vat`]);
    }
    else {
      if (this.search)
        this.router.navigate([`/system/bills-invoice-sale`], { queryParams: this.search });
      else
        this.router.navigate([`/system/bills-invoice-sale`]);
    }
  }

  viewDetailInvoice(data) {
    if (data) {
      let is_default_tempplate = true;
      const ids = [];
      ids.push(data.id);
      if (!VStringService._attemptConvertBool(data.is_default_template))
        is_default_tempplate = false;
        if (is_default_tempplate) {
          const initialState = {
            items: ids,
            isXuatHD: false,
            hinhthucxuat: '',
            typeAction: sEnum.typeCode.xem_hoa_don_mtt_html
          };
          this.vPreviewInvService.xemMauPhanTrang(initialState);
        }
        else {
          if (this.configs.system_parameter.view_multi_invoice) {
            let data = this.billService.taodulieuXemMau(ids);
            data['configs'] = sEnum.cauhinhxemmau.hoadon;
            this.vPreviewInvService.xemMau(data);
          }
          else {
            const initialState = {
              items: ids,
              isXuatHD: false,
              hinhthucxuat: '',
              typeAction: sEnum.typeCode.xem_hoa_don_mtt,
              cssClass: 'modal-xl view-bill'
            };
            this.vPreviewInvService.xemMauPhanTrang(initialState);
          }
        }
    }
  }
}
