import { Component, OnInit } from '@angular/core';

import * as moment from 'moment';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsModalService } from 'ngx-bootstrap/modal';

import { Helper, AuthService, Library, VStringService } from '@core/index';
import { VatService } from '@modules/vat/shared';

import { CustomersService } from '@modules/customers/shared';
import { SendMailComponent } from '../send-mail/send-mail.component';
import { BillService } from '@modules/bill-invoice/shared/bill.service';

@Component({
  selector: 'app-send-invoices',
  templateUrl: './send-invoices.component.html',
  styleUrls: ['./send-invoices.component.scss']
})
export class SendInvoicesComponent implements OnInit {

  frmData: any = {};
  invoice: any
  invoice_process: any;
  _translate: any;
  email: any;
  constructor(
    private authService: AuthService,
    public bsModalRef: BsModalRef,
    private vatService: VatService,
    private billService: BillService,
    private modalService: BsModalService,
    public bsModalSendMail: BsModalRef,
    private customersService: CustomersService,

  ) {
    this._translate = this.authService.getTranslate();

  }

  ngOnInit() {
    this.loadTableContent();
    let invoice = this.invoice;
    this.frmData.template = `${invoice.template_code} - ${invoice.bill_series}`;
    this.frmData.bill_date = moment(invoice.bill_date).format('DD/MM/YYYY');
    this.frmData.bill_number = invoice.bill_number;
    this.frmData.customer_code = invoice.customer_code;
    this.frmData.buyer_tax_code = invoice.buyer_tax_code;
    this.frmData.buyer_name = invoice.buyer_name;
    this.frmData.buyer_mobile = invoice.buyer_mobile;
    this.frmData.buyer_email = invoice.buyer_email;
    this.frmData.currency_code = invoice.currency_code;
    this.frmData.payment_method_name = invoice.payment_method_view_name;
    this.frmData.customer_id = invoice.customer_id;
    this.email = invoice.buyer_email;
    this.customersService.getCustomer(this.frmData.customer_id).subscribe(response => {
      if (response.status == "success") {
        let emailInCategory = "";
        const customer = response.data.customer;
        if (customer)
          emailInCategory = customer.email;
        if (this.email) {
          if (emailInCategory != "") {
            let arrEmail = emailInCategory.split(';');
            arrEmail.forEach(item => {
              if (this.email.indexOf(item) < 0)
                this.email = this.email + ";" + item;
            });
          }
        }
        else {
          this.email = emailInCategory;
        }
      }
    });
  }

  loadTableContent() {
    this.invoice_process = {};
    let sub = this.billService.getInvoiceProccess(this.invoice.id).subscribe(rs => {
      const mapObj = {
        T: "; ",
      };
      this.invoice_process = rs.data.bill_process;
      if (this.invoice_process && this.invoice_process.length > 0) {
        this.invoice_process.forEach(item => {
          item.content = VStringService._getValueToString(item.content).replace(/;/g, "; ");
        });
      }
    }, err => { }, () => {
      sub.unsubscribe();
    });
  }
  sendMail() {
    const initialState = {
      invoice_id: this.invoice.id,
      email: this.email,
      customer_id: this.frmData.customer_id
    };
    this.bsModalSendMail = this.modalService.show(SendMailComponent, { class: 'modal-md', initialState, backdrop: 'static', keyboard: false });
    this.modalService.onHide.subscribe(() => {
      if (this.bsModalRef.content.successSaveItem == 'success_mail') {
        this.loadTableContent();
      }
    });
  }
  onEditorPreparing(e) {
    if (e.dataField === 'content' && e.parentType == "dataRow") {
      e.editorName = 'dxTextArea';
    }
  }

}
