  async inhoadon(data?) {
    if (this.clickCurent) {
      document.getElementById(this.clickCurent).classList.remove('show');
    }
    if (this.listTemplate.length > 0) {
      if (this.selectedItems.length > 0 || this.dataPrint || data) {
        if (this.selectedItems.length > 0) {
          this.numberPrint = this.selectedItems.length
          const ghiTam = this.selectedItems.filter(item => !item.bill_number)
          if (ghiTam.length === 0) {
            if (this.listTemplate.length > 1) {
              if (this.isPrint) {
                this.templateService.show(this.template_id).toPromise().then(rs => {
                  if (rs.status == "success") {
                    Library.showloading();
                    this.selectedItems.forEach(item => {
                      this.createFormTypeBase64(rs.data.template, item);
                      let html = Base64.encode(this.arrHtml);
                      this.socketService.inFileHtml(() => this.connectWebsocket(), html)
                    })
                    setTimeout(() => {
                      Library.hideloading();
                      this.dataGrid.instance.clearSelection();
                    }, 1000);
                  } else {
                    return null;
                  }
                });
              } else {
                this.showPopupIn = true;
              }
            } else {
              this.templateService.show(this.listTemplate[0].id).toPromise().then(rs => {
                if (rs.status == "success") {
                  Library.showloading();
                  this.selectedItems.forEach(item => {
                    this.createFormTypeBase64(rs.data.template, item);
                    let html = Base64.encode(this.arrHtml);
                    this.socketService.inFileHtml(() => this.connectWebsocket(), html)
                  })
                  setTimeout(() => {
                    Library.hideloading();
                    this.dataGrid.instance.clearSelection();
                  }, 1000);
                } else {
                  return null;
                }
              });
            }
          } else {
            Library.notify('Vui lng chn ha n  xut', 'error')
          }
        } else {
          this.numberPrint = 1
          if (data) {
            if (data.bill_number) {
              if (this.listTemplate.length > 1) {
                if (this.isPrint) {
                  this.templateService.show(this.template_id).toPromise().then(rs => {
                    if (rs.status == "success") {
                      Library.showloading();
                      this.createFormTypeBase64(rs.data.template, data);
                      let html = Base64.encode(this.arrHtml);
                      this.socketService.inFileHtml(() => this.connectWebsocket(), html)
                      setTimeout(() => {
                        Library.hideloading();
                        this.dataGrid.instance.clearSelection();
                        this.isPrint = false;
                      }, 1000);
                    } else {
                      return null;
                    }
                  });
                } else {
                  this.showPopupIn = true;
                  this.dataPrint = data;
                }
              } else {
                this.templateService.show(this.listTemplate[0].id).toPromise().then(rs => {
                  if (rs.status == "success") {
                    Library.showloading();
                    this.createFormTypeBase64(rs.data.template, data);
                    let html = Base64.encode(this.arrHtml);
                    this.socketService.inFileHtml(() => this.connectWebsocket(), html)
                    setTimeout(() => {
                      Library.hideloading();
                      this.dataGrid.instance.clearSelection();
                    }, 1000);
                  } else {
                    return null;
                  }
                });
              }
            } else {
              Library.notify('Vui lng chn ha n  xut', 'error')
            }
          } else {
            if (this.isPrint) {
              this.templateService.show(this.template_id).toPromise().then(rs => {
                if (rs.status == "success") {
                  Library.showloading();
                  this.createFormTypeBase64(rs.data.template, this.dataPrint);
                  let html = Base64.encode(this.arrHtml);
                  this.socketService.inFileHtml(() => this.connectWebsocket(), html)
                  setTimeout(() => {
                    Library.hideloading();
                    this.dataGrid.instance.clearSelection();
                    this.dataPrint = null;
                  }, 1000);
                } else {
                  return null;
                }
              });
            } else {
              this.showPopupIn = true;
              this.dataPrint = data;
            }
          }
        }
      } else {
        Library.notify('Cha chn bn ghi no', 'error')
      }
    } else {
      Library.notify('Cha c mu phiu tnh tin', 'error')
    }
  }
