import { Injectable } from '@angular/core';
import { Router, Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { take, map } from 'rxjs/operators';
import { ListsService } from '@modules/lists/shared';
import { Library, Helper, sEnum, AuthService } from '@core/index';
import { forkJoin } from 'rxjs';
import { BillService } from './bill.service';

@Injectable()
export class ShowInvoiceResolver implements Resolve<any[]> {
  constructor(
    private billService: BillService,
    private router: Router,
    private listsService: ListsService,
    private authService: AuthService,
  ) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any[]> {
    let invoice_id = route.paramMap.get('invoiceId');
    let isViewTT32 = false;
    if (route.queryParams['status'] === 'viewInvTT32') {
      isViewTT32 = true;
    }
    Library.showloading();
    let isTT78 = false;
    let infoDangKy = this.authService.getInfoRegister();
    if (infoDangKy && infoDangKy.id) {
      if (infoDangKy.status == "CHAP_NHAN")
        isTT78 = true;
    }
    if (isTT78 == true && isViewTT32)
      isTT78 = false;
    let loaiHoaDon = route.data.type;
    let _countDm = 0;
    let dataJoin = [];
    let dataDM: any;
    /**
     * Thiu bn hc sinh sinh vin, r sot li cc hm lin quan
     */
    let arr = [
      {
        "code": "DM_NHOM_KHACH_HANG",
        "order": 0
      },
      {
        "code": "DM_LOAI_TIEN",
        "order": 0
      },
      {
        "code": "DM_HINH_THUC_THANH_TOAN",
        "order": 0
      },
      {
        "code": "DM_DON_VI_TINH",
        "order": 0
      },
      {
        "code": "DM_TY_LE_VAT",
        "order": 0
      },
      {
        "code": "DM_DINH_KHOAN",
        "order": 0
      },
      {
        "code": "DM_NOI_XUAT_HOA_DON",
        "order": 0
      },
      {
        "code": "DM_QUOC_GIA",
        "order": 0
      },
      {
        "code": "DM_TAI_KHOAN_NGAN_HANG",
        "order": 0
      },
    ];
    dataDM = this.listsService.getDMBy(arr);
    if (dataDM.length > 0) {
      dataDM.forEach(element => {
        dataJoin.push(element);
      });
    }
    _countDm = dataDM.length;
    let dataRecord: any;
    dataRecord = this.billService.showInvoiceResolverTT78(invoice_id, loaiHoaDon);
    if (dataRecord.length > 0) {
      dataRecord.forEach(element => {
        dataJoin.push(element);
      });
    }
    let dataForkJoin = forkJoin(dataJoin);
    return dataForkJoin.pipe(
      take(1),
      map(rs => {
        Library.hideloading();
        let isEdit = true;
        let status = sEnum.statusApi.success;
        for (let index = 0; index < rs.length; index++) {
          if (rs[index].status != sEnum.statusApi.success) {
            Library.notify(rs[index].message, "error");
            if (loaiHoaDon == "01GTKT")
              this.router.navigate(['/system/bills-invoice-vat']);
            else if (loaiHoaDon == "02GTTT")
              this.router.navigate(['/system/bills-invoice-sale']);
            status = sEnum.statusApi.error;
            break;
          }
        }
        if (status == sEnum.statusApi.success) {
          if (dataDM.length > 0) {
            let _data = [];
            rs.forEach((element, i) => {
              if (i < dataDM.length) {
                _data.push(element);
              }
            });
            this.authService.setDMLocal(_data);
          }
          if (loaiHoaDon == "03XKNB" || loaiHoaDon == sEnum.loaihoadon.HGDL04) {
            let templates = Helper.convertTemplatesToBillResolver(rs[dataJoin.length - 1]);
            return [
              rs[_countDm].data.products,
              rs[dataJoin.length - 3].data.bill,
              isEdit,
              templates,
              rs[dataJoin.length - 2].data.wavehourses,
            ];
          }
          else {
            let templates = Helper.convertTemplatesToBillResolver(rs[dataJoin.length - 1]);
            return [
              rs[_countDm].data.products,
              rs[dataJoin.length - 2].data.bill,
              isEdit,
              templates,
            ];
          }
        }
        else
          return null;
      })
    );
  }
}