import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { IndexComponent } from './components/index/index.component';
import { InvoiceComponent } from './components/invoice/invoice.component';
import { InvoiceResolver } from './shared/invoice.resolver';
import { ShowInvoiceResolver } from './shared/show-invoice.resolver';

const routes: Routes = [
  {
    path: '',
    component: IndexComponent
  },
  {
    path: 'create',
    component: InvoiceComponent,
    resolve: {init: InvoiceResolver},
    data: {brec: 'create_invoice'}
  },
  {
    path: ':invoiceId',
    component: InvoiceComponent,
    resolve: {init: ShowInvoiceResolver},
    data: {brec: 'invoice_details'}
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BillInvoiceRoutingModule { }
