import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { NgxPaginationModule } from 'ngx-pagination';
import { VatService } from '@modules/vat/shared';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { VStringService, SocketService, AuthService, sEnum, Library, Helper, VChanggeMoneyService, VPreviewInvService } from '@core/index';
declare var $: any;
import * as moment from 'moment';
import { Subscription } from 'rxjs/Subscription';
import { NgxSpinnerService } from 'ngx-spinner';
import { BillService } from '@modules/bill-invoice/shared/bill.service';

@Component({
  selector: 'app-view-invoice',
  templateUrl: './view-invoice.component.html',
  styleUrls: ['./view-invoice.component.scss']
})
export class ViewInvoiceBillComponent implements OnInit {
  bsModalSignRef: BsModalRef;
  items: any;
  key: string = 'name';
  reverse: boolean = false;
  objectUrl: any;
  mainTest: any;
  _language: string;
  _translate: any;
  isXuatHD: any;
  ngayHD: any;
  hinhthucxuat: any;
  configs: any;
  ioSignature: Subscription;
  lblTitle: any;
  isTaiHD: boolean = false;
  invoices = [];
  showPopup = false;
  total_payment: any;
  totalInvoice: number;
  invoiceIndex = 0;
  tempKiFile = 0;
  daXong: boolean = false;
  ganXong: boolean = false;
  valueProgress: number = 0;
  errorImport: boolean = false;
  subXuatHoaDon: any;
  isExportFailed: boolean = false;
  toltal_payment: any;
  isHoaDon = false;
  sort(key) {
    this.key = key;
    this.reverse = !this.reverse;
  }
  p: number = 1;
  widthT: any;
  heightT: any;
  typeAction: any;// XEM_HOA_DON;IN_CHUYEN_DOI;NAP_EXCEL
  ids: any;
  format = (value) => {
    let percent = Math.round(value * 100);
    return this._translate.NOTIFY.finish + ": " + percent + '%';
  }
  constructor(
    public bsModal: BsModalRef,
    private vatService: VatService,
    private router: Router,
    public translate: TranslateService,
    private authService: AuthService,
    private socketService: SocketService,
    private spinner: NgxSpinnerService,
    private billService: BillService,
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    translate.use(this._language);
  }

  ngOnInit() {
    this.connectWebsocket();
    this.configs = this.authService.getConfigs();
    this.heightT = screen.availHeight - 195;
    switch (this.typeAction) {
      case "NAP_EXCEL":
        this.lblTitle = "BN D THO HA N IN T";
        this.isTaiHD = false;
        this.isHoaDon = false;
        break;
      case "NAP_EXCEL_HD":
        this.lblTitle = "BN D THO HA N IN T";
        this.isTaiHD = false;
        this.isHoaDon= true
        break;
      case "XEM_HOA_DON":
        this.lblTitle = "XEM HA N IN T";
        this.isTaiHD = true;
        break;
      case "IN_CHUYEN_DOI":
        this.lblTitle = "IN CHUYN I HA N IN T";
        this.isTaiHD = true;
        break;
    }
    this.getPage(1, 'desc', 'created');
  }
  viewTransport(item) {
    let id = item.id;
  }

  getPage(page: number, sort: string = 'desc', order: string = 'created') {
    switch (this.typeAction) {
      case "NAP_EXCEL":
        this.action_napexcel(page);
        break;
      case "NAP_EXCEL_HD":
        this.action_napexcel(page);
        break;
      case "XEM_HOA_DON":
        this.action_xemhoadon(page);
        break;
      case "IN_CHUYEN_DOI":
        this.action_inchuyendoi(page);
        break;
    }
  }
  action_napexcel(page) {
    this.p = page;
    let oTransport: any;
    let index = 1;
    for (let item of this.items) {
      if (index == page) {
        oTransport = item;
        break;
      }
      index = index + 1;
    }
    this.spinner.show();
    let invoicesArr = [];
    invoicesArr.push(oTransport);
    let data = {
      bills: invoicesArr
    }
    let sub = this.billService.previewInvoiceImport(data).subscribe(rs => {
      if (rs.status == 'success') {
        //Xem ha n t server
        if (this.configs.system_parameter.display_template_from_server) {
          this.toltal_payment = rs.data.invoice.toltal_payment;
          this.objectUrl = rs.data.invoice.pdf_file;
          this.total_payment = rs.data.invoice.total_payment;
          this.ngayHD = moment(rs.data.invoice.invoice_date).format('DD/MM/YYYY');
          this.mainTest = "data:application/pdf;base64," + this.objectUrl;
          let iframe = document.getElementById('preview-iframe');
          iframe.setAttribute("src", this.mainTest);
          this.spinner.hide();
        }
        else {
          /* Xem ha n t Client */
          this.total_payment = rs.data.invoice.total_payment;
          let invoice_date = rs.data.invoice.invoice_date;
          let json_data = rs.data.invoice.json_data;
          let appCode = "REPX";
          if (rs.data.invoice.is_html_template)
            appCode = "HTML";
          this.socketService.xemhoadon(() => this.connectWebsocket(), json_data, appCode, "XEM_TRUOC_KHI_XUAT", this.hinhthucxuat, invoice_date, this.toltal_payment);
        }
      } else {
        Library.notify(rs.message, 'error');
        this.spinner.hide();
      }
    }, err => {
      this.spinner.hide();
    }, () => {
      sub.unsubscribe();
    });
  }
  action_xemhoadon(page) {
    this.p = page;
    let id: any;
    let index = 1;
    for (let item of this.items) {
      if (index == page) {
        id = item;
        break;
      }
      index = index + 1;
    }
    this.spinner.show();
    let arrId = [];
    arrId.push(id);
    let sub = this.billService.view(arrId).subscribe(rs => {
      if (rs.status == 'success') {
        //Xem ha n t server
        if (this.configs.system_parameter.display_template_from_server) {
          this.objectUrl = rs.data.invoice.pdf_file;
          this.ngayHD = moment(rs.data.invoice.invoice_date).format('DD/MM/YYYY');
          this.mainTest = "data:application/pdf;base64," + this.objectUrl;
          let iframe = document.getElementById('preview-iframe');
          iframe.setAttribute("src", this.mainTest);
          this.spinner.hide();
        }
        else {
          /* Xem ha n t Client */
          let invoice_date = rs.data.invoice.invoice_date;
          let json_data = rs.data.invoice.json_data;
          let appCode = "REPX";
          if (rs.data.invoice.is_html_template)
            appCode = "HTML";
          this.socketService.xemhoadon(() => this.connectWebsocket(), json_data, appCode, "XEM_TRUOC_KHI_XUAT", this.hinhthucxuat, invoice_date);
        }
      } else {
        this.spinner.hide();
        Library.notify(rs.message, 'error');
      }
    }, err => {
      this.spinner.hide();
    }, () => {
      sub.unsubscribe();
    });
  }
  action_inchuyendoi(page) {
    this.p = page;
    let id: any;
    let index = 1;
    for (let item of this.items) {
      if (index == page) {
        id = item;
        break;
      }
      index = index + 1;
    }
    this.spinner.show();
    let arrId = [];
    arrId.push(id);
    let sub = this.vatService.inChuyenDoiInvoiceImport(arrId, true).subscribe(rs => {
      if (rs.status == 'success') {
        //Xem ha n t server
        if (this.configs.system_parameter.display_template_from_server) {
          this.objectUrl = rs.data.invoice.pdf_file;
          this.ngayHD = moment(rs.data.invoice.invoice_date).format('DD/MM/YYYY');
          this.mainTest = "data:application/pdf;base64," + this.objectUrl;
          let iframe = document.getElementById('preview-iframe');
          iframe.setAttribute("src", this.mainTest);
          this.spinner.hide();
        }
        else {
          /* Xem ha n t Client */
          let invoice_date = rs.data.invoice.invoice_date;
          let json_data = rs.data.invoice.json_data;
          let appCode = "REPX";
          if (rs.data.invoice.is_html_template)
            appCode = "HTML";
          this.socketService.xemhoadon(() => this.connectWebsocket(), json_data, appCode, "XEM_TRUOC_KHI_XUAT", this.hinhthucxuat, invoice_date);
        }
      } else {
        Library.notify(rs.message, 'error');
        this.spinner.hide();
      }
    }, err => {
      this.spinner.hide();
    }, () => {
      sub.unsubscribe();
    });
  }
  dong() {
    // this.router.navigate(['/system/transport-invoice']);
    this.bsModal.hide()
  }
  dongy() {
    this.bsModal.content.successSaveItem = 'DONG_Y';
    this.bsModal.hide();
  }
  /* Kt ni n socket */
  connectWebsocket() {
    this.ioSignature = this.socketService.lapHoaDon().subscribe(message => {
      const data: any = message;
      if (data.SvcName === 'efy_viewInvoice') {
        if (data.Status == "success")
          this.viewInvoice(data.Data.Content, data.Data.typeView, data.Data.hinhthucxuat, data.Data.invoice_date);
        else if (data.Status == "error") {
          this.spinner.hide();
          Library.notify(data.Description, 'error');
        }
      }
    });
  }
  ngOnDestroy() {
    this.ioSignature.unsubscribe();
  }
  viewInvoice(content, typeView, hinhthucxuat, invoice_date) {
    this.objectUrl = content;
    this.ngayHD = moment(invoice_date).format('DD/MM/YYYY');
    this.mainTest = "data:application/pdf;base64," + this.objectUrl;
    let iframe = document.getElementById('preview-iframe');
    iframe.setAttribute("src", this.mainTest);
    this.spinner.hide();
  }
  taihoadon() {
    this.invoices = this.items
    this.showPopup = true;
    this.totalInvoice = Math.floor(this.invoices.length / 10) + 1;
    this.invoiceIndex = 0;
    this.tempKiFile = 0;
    this.xulyTaiFilePdf();
  }
  xulyTaiFilePdf() {
    let ids = this.iterator(this.invoices, 10, this.invoiceIndex);
    let is_convert = true;
    if (this.typeAction == "XEM_HOA_DON")
      is_convert = false
    let sub = this.vatService.xuatHDPdf(ids, is_convert).subscribe(rs => {
      let isSuccess = false;
      this.ganXong = true;
      this.tempKiFile = 0;
      if (rs.status == 'success') {
        Helper.dowloadFile(rs.data.file_name, 'zip', rs.data.file_content);
        isSuccess = true;
      } else {
        this.isExportFailed = true;
      }
      this.continuteExportInvoice(isSuccess);
    }, err => { }, () => {
      sub.unsubscribe();
      Library.hideloading();
    });
  }
  continuteExportInvoice(isSuccess) {
    let percentComplete = 100 / this.totalInvoice;
    this.valueProgress += percentComplete;
    if (this.invoiceIndex == this.totalInvoice - 1) {
      if (!this.isExportFailed && isSuccess)
        Library.notify("Ti ha n thnh cng", 'success', 5000);
      else {
        // Danh sch li
      }
      this.daXong = true;
      setTimeout(() => {
        this.showPopup = false;
      }, 300);
    }
    else if (this.invoiceIndex < this.totalInvoice - 1) {
      this.invoiceIndex++;
      this.xulyTaiFilePdf();
    }
  }
  iterator(a, take, skip) {
    let current = skip * 10;
    let l = a.length;
    let end = current + take;
    let part = a.slice(current, end);
    current = end < l ? end : 0;
    return part;
  }
  onImportShowing(e) {
    this.valueProgress = 0;
  }

  onImportHiding(e) {
    let r = true;
    if (this.valueProgress < 100 && this.errorImport == false) {
      r = confirm("Cnh bo: Qu trnh ti ha n s dng li nu bn ng ca s ny?");
    }
    if (r) {
      if (this.subXuatHoaDon) {
        this.subXuatHoaDon.unsubscribe();
      }
      this.valueProgress = 0;
    } else {
      e.cancel = true;
    }
  }
}
